.hystmodal__opened {
	position: fixed;
	right: 0;
	left: 0;
	overflow: hidden;
}
.hystmodal__shadow{
	position: fixed;
	border:none;
	display: block;
	width: 100%;
	top: 0;
	bottom: 0;
	right: 0;
	left: 0;
	overflow: hidden;
	pointer-events: none;
	z-index: 98;
	opacity: 0;
	transition: opacity 0.15s ease;
	background-color: black;
}
.hystmodal__shadow--show{
	pointer-events: auto;
	opacity: 0.6;
}
.hystmodal {
	position: fixed;
	top: 0;
	bottom: 0;
	right: 0;
	left: 0;
	overflow: hidden;
	overflow-y: auto;
	-webkit-overflow-scrolling: touch;
	opacity: 1;
	pointer-events: none;
	display: flex;
	flex-flow: column nowrap;
	justify-content: flex-start;
	z-index: 99;
	visibility: hidden;
}
.hystmodal--active{
	opacity: 1;
}
.hystmodal--moved,
.hystmodal--active{
	pointer-events: auto;
	visibility: visible;
}
.hystmodal__wrap {
	flex-shrink: 0;
	flex-grow: 0;
	width: 100%;
	min-height: 100%;
	margin: auto;
	display: flex;
	flex-flow: column nowrap;
	align-items: center;
	justify-content: center;
}
.hystmodal__window {
	margin: 50px 0;
	box-sizing: border-box;
	flex-shrink: 0;
	flex-grow: 0;
	background: #fff;
	width: 600px;
	max-width: 100%;
	overflow: visible;
	transition: transform 0.2s ease 0s, opacity 0.2s ease 0s;
	transform: scale(0.9);
	opacity: 0;
}
.hystmodal--active .hystmodal__window{
	transform: scale(1);
	opacity: 1;
}
